import React from 'react';
import './Loading.css';

export interface LoadingProps {
  onComplete?: () => void;
}

export const Loading: React.FC<LoadingProps> = ({ onComplete }) => {
  React.useEffect(() => {
    if (onComplete) {
      onComplete();
    }
  }, [onComplete]);

  return (
    <div className="loading-container">
      <div className="loading-spinner"></div>
    </div>
  );
};
